import React, { Component } from 'react';

class HomeBotton extends Component {

  render() {
    return (
      <div id="page-title" className="home hidden">
        <div
          style={{ cursor: 'pointer' }}
          onClick={() => {
            window.scrollTo(0, 0);
            window.location.reload();
          }}
          className="name"
        >
          laon. <span>studio</span>
        </div>
      </div>
    );
  }

}

export default HomeBotton;



