import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';

import Footer from '../../containers/footer/Footer';
import Menu from '../../containers/menu/Menu';

import Hamburger from '../../components/hamburger/Hamburger';
import HomeButton from '../../components/buttons/HomeButton';

class Taskhunters extends Component {
  componentDidMount() {
    window.sr.reveal(".cover", window.cover);

    window.sr.reveal('.immagine', window.immagini);
    window.sr.reveal('.image', window.immagini);

    window.sr.reveal(".herotitle", window.herotitle);
    window.sr.reveal(".description", window.herotitle);

    window.sr.reveal(".text", window.text);

    window.sr.reveal(".sectitle", window.sectitle);
    window.sr.reveal(".freccia", window.arrow);
    window.sr.reveal('.qanda', window.qanda, 100);
    window.sr.reveal('.project', window.projects, 100);
    window.sr.reveal(".contact", window.contact);
    window.sr.reveal(".footer", window.footer, 120);
  }

  // componentWillUnmount() {
  //   setTimeout(() => window.location.reload(), 100);
  //   setTimeout(() => window.scrollTo(0, 0), 110);
  // }

  render() {
    const { history } = this.props;
    return (
      <div id="main">
        <Menu />
        <div className="container">
          <Hamburger />
          <HomeButton />
        </div>
        <div className="projectpage">
          {/* <a className="freccia" href="#list"></a> */}
          <div className="hero taskhunters">
            <div className="container">
              <h1 onClick={() => history.push('/')} className="brand" style={{ cursor: 'pointer' }}>laon.</h1>
            </div>
          </div>
          <div className="container paragrafo">
            <h2 className="projecttitle herotitle col-xs-10 col-sm-8 mx-auto">Taskhunters for Business is a virtual marketplace that lets companies find students and assign them tasks to be solved.</h2>
            <p className="description col-xs-10 col-sm-8 mx-auto">Companies and students have a really different point of view, the challenge was to design a solution to let them collaborate. We made it institutional but cool and above all clear and easy to use.</p>
          </div>
          <div className="img_full secondcover">
            <img className="immagine" src="/images/projects/taskhunters/00_cover_taskhunters.jpg" alt="Taskhunters Cover" />
          </div>
          <div className="container">
            <p className="text col-xs-10 col-sm-8 mx-auto">At this initial phase of the design process, our ideas are still blooming and require a lot of work. Wireframing serves to establish the relationship between elements, allowing you to decide on the websites navigation and structure without major consequence.</p>
          </div>
          <div className="img_background colore taskhunters">
            <img className="immagine" src="/images/projects/taskhunters/02_wireframes.png" alt="Taskhunters Wireframe" height="838px" width="977px" />
          </div>
          <div className="container paragrafo">
            <p className="text col-xs-10 col-sm-8 mx-auto">The next step was designing style guidelines. The new design language follows the brand's character but now includes a new personality: the platform isn't only institutional but also youthful and clear.</p>
          </div>
          <div className="img_background">
            <img className="immagine" src="/images/projects/taskhunters/03_design_guidelines.png" alt="Taskhunters Design guidelines" width="780px" height="1017px" />
          </div>
          <div className="container paragrafo">
            <p className="text col-xs-10 col-sm-8 mx-auto">The difference between something good and something great is the attention to details. For this reason, we worked with "pixel-perfect" precision, designing something amazing.</p>
          </div>
          <div className="img_background colore taskhunters">
            <img className="immagine" src="/images/projects/taskhunters/04_ui_kit.png" alt="Taskhunters UI Kit" width="1183px" height="2320px" />
          </div>
          <div className="container paragrafo">
            <p className="text col-xs-10 col-sm-8 mx-auto">We designed the entire on-boarding process to allow users, in few simple steps, to create and set their account, according to their fields of competence and availability.</p>
          </div>
          <div className="img_background colore taskhunters">
            <img className="immagine" src="/images/projects/taskhunters/05_onboarding.png" alt="Taskhunters Onboarding" width="1300px" height="863px" />
          </div>
          <div className="container paragrafo">
            <p className="text col-xs-10 col-sm-8 mx-auto">Students and companies have dedicated dashboards that immediately shows stats and the most important news about their activity, allowing to increase the interaction with the platform.</p>
          </div>
          <div className="img_background colore taskhunters">
            <img className="immagine" src="/images/projects/taskhunters/06_dashboard.png" alt="Taskhunters Dashboards" width="1170px" height="2181px" />
          </div>
          <div className="container paragrafo">
            <p className="text col-xs-10 col-sm-8 mx-auto">We designed Taskhunters for business to let companies easily create tasks and manage students who applied and have all the informations about them.</p>
          </div>
          <div className="img_background colore taskhunters">
            <img className="immagine" src="/images/projects/taskhunters/07.1_creation_task.png" alt="Taskhunters Task creation" width="1170px" height="5507px" />
          </div>
          <div className="container paragrafo">
            <p className="text col-xs-10 col-sm-8 mx-auto">Student's profile shows everything that companies are looking for as bios, skills and reviews. Students can add an intro message to their applications to stand up from the crowd catching the attention of recruiters.</p>
          </div>
          <div className="img_background colore taskhunters">
            <img className="immagine" src="/images/projects/taskhunters/07.2_creation_task.png" alt="Taskhunters Task creation 2" width="1170px" height="1337px" />
          </div>
          <div className="container paragrafo">
            <p className="text col-xs-10 col-sm-8 mx-auto">The payment process is automatized and companies can explore all their data and information inside the platform but they can also export everything with few clicks.</p>
          </div>
          <div className="img_background colore taskhunters">
            <img className="immagine" src="/images/projects/taskhunters/07.3_creation_task.png" alt="Taskhunters Administration" width="1170px" height="737px" />
          </div>
          <div className="container paragrafo">
            <p className="text col-xs-10 col-sm-8 mx-auto">Find a design language that can easily adapt to both students and companies is not that easy. We tried hard and we ended up with two landing pages that show with a clear and nice interface the right informations to both user types.</p>
          </div>
          <div className="img_full">
            <img className="immagine" src="/images/projects/taskhunters/08_landing.png" alt="Taskhunters Landings" />
          </div>
          <div className="container paragrafo">
            <p className="text col-xs-10 col-sm-8 mx-auto">Communications with users are really important, we designed different e-mail templates for sign-ups, general communications, advertising and also a business e-mail template.</p>
          </div>
          <div className="img_full">
            <img className="immagine" src="/images/projects/taskhunters/09_email_templates.png" alt="Taskhunters E-mail templates" />
          </div>
          <div className="container paragrafo">
            <p className="text col-xs-10 col-sm-8 mx-auto">To create something exceptional, we have been focused on the smallest details. Inputs, dropdowns, modals, graphs, buttons, typography: all of these elements have been thoroughly studied to look good on their own and matching perfect together creating something really consistent.</p>
          </div>
          <div className="img_full">
            <img className="immagine" src="/images/projects/taskhunters/10_ui_elements.png" alt="Taskhunters elements" />
          </div>
          <div className="container">
            <Footer />
          </div>
        </div>
      </div>
    );
  }

}

export default withRouter(Taskhunters);



