import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';

import Footer from '../../containers/footer/Footer';
import Menu from '../../containers/menu/Menu';

import Hamburger from '../../components/hamburger/Hamburger';
import HomeButton from '../../components/buttons/HomeButton';

class Home extends Component {
  componentDidMount() {
    window.sr.reveal(".herotitle", window.herotitle);
    window.sr.reveal(".sectitle", window.sectitle);
    window.sr.reveal(".freccia", window.arrow);
    window.sr.reveal('.qanda', window.qanda, 100);
    window.sr.reveal('.project', window.projects,100);
    window.sr.reveal(".contact", window.contact);
    window.sr.reveal(".footer", window.footer, 120);
    window.sr.reveal(".cover", window.cover);
  }

  // componentWillUnmount() {
  //   setTimeout(() => window.location.reload(), 100);
  //   setTimeout(() => window.scrollTo(0, 0), 110);
  // }

  render() {
    const { history } = this.props;
    return (
      <div id="main">
        <Menu />
        <div className="container">
          <Hamburger />
          <HomeButton />
          <section className="hero">
            <h1 onClick={() => history.push('/')} className="brand" style={{ cursor: 'pointer', fontFamily: 'NamumSquare' }}>.</h1>
            <h2 className="bigtitle herotitle">Jae-Hun Cho<br /><span> Interactive Front-End Developer</span></h2>
            <a className="freccia" href="#list"> </a>
          </section>

          <section id="about">
            <h2 className="bigtitle sectitle">I design and simplify digital products<br /><span>with a user-centric and data-driven approach</span></h2>
            <div className="row qa justify-content-center">
                <div className="col-md-2 question qanda"><h3>About me</h3></div>
                <div className="col-md-5 answer qanda">
                    <p>I am a rapidly growing youth of open-minded person, sometimes a little crazy, perfectionists, who love white space and observe every detail. I'm designers, developers, product managers but above all person who love what they do and always give their best.</p>
                </div>
                <div className="col-md-5 answer qanda">
                    <p>I work side by side with customers who trust me. This together with a user-centric and data-driven approach allows me to create consistent experiences across all media and create exceptional products.</p>
                    <div className="link" onClick={() => history.push('/about')}>Discover more</div>
                </div>
            </div>
          </section>

              <section id="projects">
                  <h2 className="bigtitle sectitle">Join projects<br /><span>made with passion</span></h2>
                  <div className="row">
                      <div className="col-12 col-md-6 project">
                        <div
                          style={{ cursor: 'pointer' }}
                          onClick={() => history.push('/projects/taskhunters')}
                        >
                          <div className="work taskhunters"></div>
                          <div>
                            <h2>Taskhunters for Business</h2>
                            <p className="description">A virtual marketplace that lets companies find students and assign them tasks to be solved.</p>
                          </div>
                        </div>
                      </div>
                      <div className="col-12 col-md-6 project">
                        <div
                          style={{ cursor: 'pointer' }}
                          onClick={() => history.push('/projects/spedity')}
                        >
                          <div className="work spedity"></div>
                          <div>
                            <h2>Spedity</h2>
                            <p className="description">A free, fast and secure application that brings together carriers with shippers.</p>
                          </div>
                        </div>
                      </div>
                      <div className="col-12 col-md-6 project">
                        <div
                          style={{ cursor: 'pointer' }}
                          onClick={() => history.push('/projects/taskhuntersapp')}
                        >
                          <div className="work taskhuntersapp"></div>
                          <div>
                            <h2>Taskhunters app</h2>
                            <p className="description">Taskhunters lets busy people find students and assign them tasks to be solved.</p>
                          </div>
                        </div>
                      </div>
                  </div>
              </section>


            <Footer />

        </div>

      </div>
    );
  }
}

export default withRouter(Home);



