import React, { Component } from 'react';

class Hamburger extends Component {

  render() {
    return (
      <div className="hamburger hidden">
        <a className="menu-btn" href="#">
          <img src="/images/hamburger.svg" alt=""/>
        </a>
      </div>
    );
  }

}

export default Hamburger;



