import React, { Component } from 'react';

import Footer from '../../containers/footer/Footer';
import Menu from '../../containers/menu/Menu';

import Hamburger from '../../components/hamburger/Hamburger';
import HomeButton from '../../components/buttons/HomeButton';

class About extends Component {
  componentDidMount() {
    window.sr.reveal(".herotitle", window.herotitle);
    window.sr.reveal(".sectitle", window.sectitle);
    window.sr.reveal(".freccia", window.arrow);
    window.sr.reveal('.qanda', window.qanda, 100);
    window.sr.reveal('.project', window.projects,100);
    window.sr.reveal(".contact", window.contact);
    window.sr.reveal(".footer", window.footer, 120);
    window.sr.reveal(".cover", window.cover);
    window.sr.reveal(".second_page_img", window.second_page_img);

    document.getElementById('gifricerca').src="/images/about/research.gif?a="+Math.random();

    document.getElementById('gifdesign').src="/images/about/design.gif?a="+Math.random();

    document.getElementById('gifsviluppo').src="/images/about/sviluppo.gif?a="+Math.random();

  }

  // componentWillUnmount() {
  //   setTimeout(() => window.location.reload(), 100);
  //   setTimeout(() => window.scrollTo(0, 0), 110);
  // }


  render() {
    const { history } = this.props;
    return (
      <div id="main">
        <Menu />

        <div className="container">
          <Hamburger />
          <HomeButton />

          <section className="hero second-page">
            <h1 onClick={() => history.push('/')} className="brand" style={{ cursor: 'pointer' }}>laon.</h1>
            <h2 className="secondtitle herotitle">About<br /><span> why, what and how</span></h2>
            <div className="hero_second_img second_page_img">
              <div className="image"></div>
            </div>
            {/* <a className="freccia" href="#list"></a> */}
          </section>

          <section className="about-page">
          <h2 className="bigtitle sectitle">Design meets strategy and technology<br /><span>Perfect formula to help your business grow</span></h2>
          <div className="row qa">
            <div className="col-md-3 question qanda padding"><h3>Our why</h3></div>
            <div className="col-md-6 answer qanda">
              <p>We believe in the beauty of simplicity and its power. We believe that simplifying serves to eliminate the superfluous and make sure that the necessary can stand out as it deserves. <br />This is why we love white spaces and observe every detail, even the one you probably did not notice.<br /><br />We are able to improve human experiences, we think, we design and improve products that make everyday life better, work but also game.<br /><br />We set ourselves the goal of finding simple solutions to complex problems. We take advantage of our maniacal attention to detail to take care of strategy, design and development.</p>
            </div>
          </div>
          <div className="row qa">
            <div className="col-md-3 question qanda"><h3>Our process</h3></div>
            <div className="col-md-6 answer qanda">
              <img id="gifricerca" src="" width="190px" height="190px" alt="" />

              <h3>Research and Strategy</h3>
              <p>We have experience in our field but not yours, your collaboration is fundamental, we must meet your world, the one you know well. We analyze it and define what you need to get to your goal.</p>
            </div>
            <div className="col-md-6 answer qanda offset-md-3">
              <img id="gifdesign" src="" width="190px" height="190px" alt="" />

              <h3>Design</h3>
              <p>We believe that simplicity is the key to everything. We do not like redundant information and reduce visual noise to minimum. We believe that white space between the elements creates the design as the pauses between the notes create a melody.</p>
            </div>
            <div className="w-100"></div>
            <div className="col-md-6 answer qanda offset-md-3">
              <img id="gifsviluppo" src="" width="190px" height="190px" alt="" />

              <h3>Develop</h3>
              <p>This is not the right place to be too technical, you just need to know that we develop with the same care with which we design. Everything looks beautiful and works great, and that's what matters.</p>
            </div>
          </div>
          <h2 className="bigtitle sectitle">We are here to support you and your project<br /><span>and we promise to do our best</span></h2>
          <div className="row qa">
            <div className="col-md-3 question qanda"><h3>We are expert in</h3></div>
            <div className="col-md-4 answer qanda">
              <h3>Product design</h3>
              <p>Whether it's an online platform, a mobile app, or a project that leverages augmented reality or artificial intelligence, we'll help you create your product at all stages.</p>
            </div>
            <div className="col-md-4 answer qanda offset-md-1">
              <h3>User Experience / User Interface</h3>
              <p>Do you have a product that is based on a fantastic idea but does not make what you expected? Let's find out together the reasons and data in hand, we try to improve the experience of your users.</p>
            </div>
            <div className="col-md-4 answer qanda offset-md-3">
              <h3>E-commerce</h3>
              <p>A digital shop, open 24/7 and 365 days a year. We help you create a fantastic experience for your customers and make it consistent with that of physical stores.</p>
            </div>
            <div className="col-md-4 answer qanda offset-md-1">
              <h3>Brand</h3>
              <p>"The habit does not make the monk", but is it really like that? We think that the first impression is really important and that we must always present ourselves in the best possible way.</p>
            </div>
          </div>

          <h2 className="bigtitle sectitle">We made it with love and passion<br /><span>and we take it very seriously</span></h2>
          <div className="row qa">
            <div className="col-md-3 question qanda"><h3>Awards</h3></div>
            <div className="col-md-9">
              <table className="table qanda">
                <tbody>
                  <tr>
                    <th scope="row">2018</th>
                    <td className="name">Mono.</td>
                    <td>Honorable Mention</td>
                    <td>Awwwards</td>
                  </tr>
                  <tr>
                    <th scope="row">2018</th>
                    <td className="name">Mono.</td>
                    <td>Site of the Day</td>
                    <td>Mindsparkle Mag</td>
                  </tr>
                  <tr>
                    <th scope="row">2018</th>
                    <td className="name">Mono.</td>
                    <td>Special Kudos</td>
                    <td>CSS Design Awards</td>
                  </tr>
                  <tr>
                    <th scope="row">2018</th>
                    <td className="name">Mono.</td>
                    <td>Best UI - UX - Innovation</td>
                    <td>CSS Design Awards</td>
                  </tr>
                  <tr>
                    <th scope="row">2018</th>
                    <td className="name">Taskhunters for Business</td>
                    <td>Silver Award</td>
                    <td>International Design Awards</td>
                  </tr>
                  <tr>
                    <th scope="row">2018</th>
                    <td className="name">Taskhunters for Business</td>
                    <td>DDAW</td>
                    <td>Digital Design Awards</td>
                  </tr>
                  <tr>
                    <th scope="row">2018</th>
                    <td className="name">Spedity</td>
                    <td>DDAW</td>
                    <td>Digital Design Awards</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </section>

        <Footer />

        </div>
      </div>
    );
  }

}

export default About;



