import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';

import Footer from '../../containers/footer/Footer';
import Menu from '../../containers/menu/Menu';

import Hamburger from '../../components/hamburger/Hamburger';
import HomeButton from '../../components/buttons/HomeButton';

class Contacts extends Component {
  componentDidMount() {
    window.sr.reveal(".herotitle", window.herotitle);
    window.sr.reveal(".sectitle", window.sectitle);
    window.sr.reveal(".freccia", window.arrow);
    window.sr.reveal('.qanda', window.qanda, 100);
    window.sr.reveal('.project', window.projects,100);
    window.sr.reveal(".contact", window.contact);
    window.sr.reveal(".footer", window.footer, 120);
    window.sr.reveal(".cover", window.cover);
    window.sr.reveal(".second_page_img", window.second_page_img);
  }

  // componentWillUnmount() {
  //   setTimeout(() => window.location.reload(), 100);
  //   setTimeout(() => window.scrollTo(0, 0), 110);
  // }

  render() {
    const { history } = this.props;
    return (
      <div id="main">
        <Menu />
        <div className="container">
          <Hamburger />
          <HomeButton />
          <section className="hero second-page">
            <h1 onClick={() => history.push('/')} className="brand" style={{ cursor: 'pointer' }}>laon.</h1>
            <h2 className="secondtitle herotitle">Contacts<br /><span> the best way to get in contact</span></h2>
            <div className="hero_second_img second_page_img contact">
              <div className="image"></div>
            </div>
            {/* <a className="freccia" href="#list"></a> */}
          </section>

          <section className="about-page">
            <h2 className="bigtitle sectitle">You can find us in a magic place<br /><span>between the sea and a volcano</span></h2>
            <div className="row qa">
              <div className="col-md-3 question qanda"><h3>My magic place</h3></div>
              <div className="col-md-6 answer qanda">
                <h3>Seoul</h3>
                <p>17, Hakdong-ro 42-gil, Gangnam-gu<br />ZIP code - 06098<br />Mobile: +82 10 22954394</p>
              </div>
            </div>
            <div className="row qa">
              <div className="col-md-3 question qanda"><h3>My mail</h3></div>
              <div className="col-md-4 answer qanda">
                <h3>Click me</h3>
                <a className="maillink" href="mailto:cj4luv@googel.com">cj4luv@googel.com</a>
              </div>
              {/* <div className="col-md-4 offset-md-1 answer qanda">
                <h3>Careers</h3>
                <a className="maillink" href="mailto:jobs@monodigital.studio">jobs@monodigital.studio</a>
              </div>
              <div className="col-md-4 offset-md-3 answer qanda">
                <h3>Everything else</h3>
                <a className="maillink" href="mailto:hello@monodigital.studio">hello@monodigital.studio</a>
              </div> */}
            </div>
          </section>
          <section id="contact">
            <Footer />
          </section>
        </div>
      </div>
    );
  }

}

export default withRouter(Contacts);



