import React, { PureComponent } from 'react';
import { Route, withRouter } from 'react-router-dom';

import Home from '../pages/home/Home';
import About from '../pages/about/About';
import Projects from '../pages/projects/Projects';
import Careers from '../pages/careers/Careers';
import Contacts from '../pages/contacts/Contacts';
import Taskhunters from '../pages/taskhunters/Taskhunters';
import Spedity from '../pages/spedity/Spedity';
import Taskhuntersapp from '../pages/taskhuntersapp/Taskhuntersapp';

class RootRouter extends PureComponent {

  render() {
    return (
      <div>

        <Route
          exact
          path="/"
          component={Home}
        />

        <Route
          exact
          path="/about"
          component={About}
        />

        <Route
          exact
          path="/projects"
          component={Projects}
        />

        <Route
          exact
          path="/careers"
          component={Careers}
        />

        <Route
          exact
          path="/contacts"
          component={Contacts}
        />

        <Route
          exact
          path="/projects/taskhunters"
          component={Taskhunters}
        />

        <Route
          exact
          path="/projects/spedity"
          component={Spedity}
        />

        <Route
          exact
          path="/projects/taskhuntersapp"
          component={Taskhuntersapp}
        />

      </div>
    );
  }
}

export default withRouter(RootRouter);



