import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';

class Menu extends Component {

  render() {
    const { history } = this.props;
    // console.log(history.location.pathname.split('/').filter(item => item === 'projects')[0]);

    return (
      <div className="menu">
        <div className="close">
          <a href="#">
            <img src="/images/close.svg" alt=""/>
          </a>
        </div>
        <div className="container">
          <div className="row">
            <div className="col-12">
              <h1 className="brand">laon.</h1>
              <ul className="menu-list">
                <li className={history.location.pathname === '/' ? "voci-menu active" : "voci-menu"} onClick={() => history.push('/')}>
                  <p>Home</p>
                </li>
                <li role="button" className={history.location.pathname === '/about' ? "voci-menu active" : "voci-menu"} onClick={() => history.push('/about')}>
                  <p>About</p>
                </li>
                <li
                  onClick={() => history.push('/projects')}
                  className={'projects' === history.location.pathname.split('/').filter(item => item === 'projects')[0] ? "voci-menu active" : "voci-menu"}
                >
                  <p>Projects</p>
                </li>
                <li
                  onClick={() => history.push('/careers')}
                  className={history.location.pathname === '/careers' ? "voci-menu active" : "voci-menu"}
                >
                  <p>Careers</p>
                </li>
                <li
                  onClick={() => history.push('/contacts')}
                  className={history.location.pathname === '/contacts' ? "voci-menu active" : "voci-menu"}
                >
                  <p>Contacts</p>
                </li>
              </ul>
            </div>
              <div className="info col-md-12">
                <div className="question voci-menu"><h3>More about us</h3></div>
                  <ul className="links">
                      <li className="footer voci-menu"><a className="dribbble" href="https://dribbble.com/monostudio" target="_blank" rel='noopener noreferrer'>Dribbble</a></li>
                      <li className="voci-menu"><a className="behance" href="https://www.behance.net/monodigitalstudio" target="_blank" rel='noopener noreferrer'>Behance</a></li>
                      <li className="voci-menu"><a className="facebook" href="https://www.facebook.com/monodigital.studio/" target="_blank" rel='noopener noreferrer'>Facebook</a></li>
                      <li className="voci-menu"><a className="instagram" href="https://www.instagram.com/monodigital.studio/" target="_blank" rel='noopener noreferrer'>Instagram</a></li>
                      <li className="voci-menu"><a className="twitter" href="https://twitter.com/mono_digital_" target="_blank" rel='noopener noreferrer'>Twitter</a></li>
                      <li className="voci-menu"><a className="linkedin" href="https://www.linkedin.com/company/monodigitalstudio/" target="_blank" rel='noopener noreferrer'>Linkedin</a></li>
                  </ul>
              </div>
            </div>
        </div>
      </div>
    );
  }

}

export default withRouter(Menu);



