import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';

import Footer from '../../containers/footer/Footer';
import Menu from '../../containers/menu/Menu';

import Hamburger from '../../components/hamburger/Hamburger';
import HomeButton from '../../components/buttons/HomeButton';

class Careers extends Component {
  componentDidMount() {
    window.sr.reveal(".herotitle", window.herotitle);
    window.sr.reveal(".sectitle", window.sectitle);
    window.sr.reveal(".freccia", window.arrow);
    window.sr.reveal('.qanda', window.qanda, 100);
    window.sr.reveal('.project', window.projects,100);
    window.sr.reveal(".contact", window.contact);
    window.sr.reveal(".footer", window.footer, 120);
    window.sr.reveal(".cover", window.cover);
    window.sr.reveal(".second_page_img", window.second_page_img);

    window.$('.galleria').slick({
      centerMode: true,
      slidesToShow: 2,
      speed: 500,
      infinite: false,
      cssEase: 'cubic-bezier(0.6, 0.2, 0.1, 1)',
      variableWidth: true,
      prevArrow: '<a href="#" class="slick-prev"><svg id="left" width="18px" height="10px" viewBox="0 0 18 10"><g transform="translate(-231.000000, -3893.000000)" fill="#999999"><path d="M245.408078,3896.95068 L243.026069,3894.71118 C242.631125,3894.33987 242.608474,3893.71493 242.975476,3893.31534 C243.342479,3892.91576 243.960159,3892.89284 244.355103,3893.26416 L248.328502,3896.99984 C248.48048,3897.05019 248.622338,3897.13911 248.738906,3897.26603 C248.928573,3897.47253 249.014171,3897.73923 248.998095,3898 C249.014171,3898.26076 248.928573,3898.52746 248.738906,3898.73396 C248.622338,3898.86088 248.48048,3898.9498 248.328502,3899.00015 L244.355103,3902.73583 C243.960159,3903.10715 243.342479,3903.08423 242.975476,3902.68465 C242.608474,3902.28506 242.631125,3901.66012 243.026069,3901.28881 L245.539205,3898.92603 L231.976199,3898.92603 C231.437059,3898.92603 231,3898.48383 231,3897.93835 C231,3897.39288 231.437059,3896.95068 231.976199,3896.95068 L245.408078,3896.95068 Z" id="Combined-Shape" transform="translate(240.000000, 3897.999996) scale(-1, 1) translate(-240.000000, -3897.999996) "></path></g></svg></a>',
      nextArrow: '<a href="#" class="slick-next"><svg id="right" width="18px" height="10px" viewBox="0 0 18 10"><g transform="translate(-231.000000, -3893.000000)" fill="#999999"><path d="M245.408078,3896.95068 L243.026069,3894.71118 C242.631125,3894.33987 242.608474,3893.71493 242.975476,3893.31534 C243.342479,3892.91576 243.960159,3892.89284 244.355103,3893.26416 L248.328502,3896.99984 C248.48048,3897.05019 248.622338,3897.13911 248.738906,3897.26603 C248.928573,3897.47253 249.014171,3897.73923 248.998095,3898 C249.014171,3898.26076 248.928573,3898.52746 248.738906,3898.73396 C248.622338,3898.86088 248.48048,3898.9498 248.328502,3899.00015 L244.355103,3902.73583 C243.960159,3903.10715 243.342479,3903.08423 242.975476,3902.68465 C242.608474,3902.28506 242.631125,3901.66012 243.026069,3901.28881 L245.539205,3898.92603 L231.976199,3898.92603 C231.437059,3898.92603 231,3898.48383 231,3897.93835 C231,3897.39288 231.437059,3896.95068 231.976199,3896.95068 L245.408078,3896.95068 Z" id="Combined-Shape"></path></g></svg></a>',
      responsive: [
        {
          breakpoint: 768,
          settings: {
            centerPadding: '0px',
            slidesToShow: 1,
          }
        },
        {
          breakpoint: 480,
          settings: {
            centerMode: true,
            centerPadding: '0px',
            slidesToShow: 1,
          }
        }
      ]
    });
  }

  // componentWillUnmount() {
  //   setTimeout(() => window.location.reload(), 100);
  //   setTimeout(() => window.scrollTo(0, 0), 110);
  // }

  render() {
    const { history } = this.props;
    return (
      <div id="main">
        <Menu />
        <div className="container">
          <Hamburger />
          <HomeButton />

          <section className="hero second-page">
            <h1 onClick={() => history.push('/')} className="brand" style={{ cursor: 'pointer' }}>laon.</h1>
            <h2 className="secondtitle herotitle">Careers<br /><span> Thats our hobby, not our job</span></h2>
            <div className="hero_second_img second_page_img careers">
              <div className="image"></div>
            </div>
            {/* <a className="freccia" href="#list"></a> */}
          </section>

          <section className="about-page">
            <h2 className="bigtitle sectitle">We are always looking for talented people<br /><span>who love this job as much as we do</span></h2>
            <div className="row qa">
                <div className="col-md-3 question qanda padding"><h3>Our vision</h3></div>
                <div className="col-md-6 answer qanda">
                    <p>We can not do without what we do. We love it. And it's great when you do what you like with people who are passionate about it.<br />If you're talented and nice like you, everything becomes even more interesting, you create the ideal work environment, the environment where you can improve yourself and keep pushing beyond your limits. The environment in which to be the professionals who give the maximum to impose new standards instead of following them.<br /><br />We expect a lot from each other, we always give a helping hand when needed and do not wait for permission to make a choice rather than another. We know that we learn a lot from mistakes and we take this responsibility.<br /><br />We work on projects we are proud of, we give our best and work hard but at the same time we have lots of fun.</p>
                </div>
            </div>

            <h2 className="bigtitle sectitle">A lot of reasons but on top<br /><span>our fundamentals</span></h2>
            <div className="row qa">
              <div className="col-md-3 question qanda"><h3>Our "laws"</h3></div>
              <div className="col-md-4 answer qanda">
                  <h3>We do not work for everyone</h3>
                  <p>We only choose projects in which we strongly believe, each project involves us completely and we can not make the mistake of working for a project that is far from our values.</p>
              </div>
              <div className="col-md-4 answer qanda offset-md-1">
                  <h3>We have no levels</h3>
                  <p>We believe everyone has to learn from anyone. This is why we believe that having different levels is useless. We are all "designers" or "developers" but it does not matter if you are a junior or a senior.</p>
              </div>
              <div className="col-md-4 answer qanda offset-md-3">
                  <h3>Quality over quantity</h3>
                  <p>We immerse ourselves in our every project and we give the maximum, everything must be taken care of and studied. We are not interested in working on approximate and untreated projects.</p>
              </div>
              <div className="col-md-4 answer qanda offset-md-1">
                  <h3>Respect and kindness</h3>
                  <p>For a healthy environment, respect and kindness are a fundamental rule that everyone must respect within the team and we are not going to turn a blind eye.</p>
              </div>
              <div className="col-md-4 answer qanda offset-md-3">
                  <h3>Responsibility</h3>
                  <p>Everyone must be able to make decisions and move on without stopping any process. If you join the team it's because we believe you'll be able to do it.</p>
              </div>
            </div>
          </section>
        </div>

        <div className="galleria qanda">
          <div><img src="/images/careers/galleria/1.jpg" width="870px" height="540px" alt="" /></div>
          <div><img src="/images/careers/galleria/2.jpg" width="870px" height="540px" alt="" /></div>
          <div><img src="/images/careers/galleria/3.jpg" width="870px" height="540px" alt="" /></div>
          <div><img src="/images/careers/galleria/6.jpg" width="870px" height="540px" alt="" /></div>
          <div><img src="/images/careers/galleria/4.jpg" width="870px" height="540px" alt="" /></div>
          <div><img src="/images/careers/galleria/5.jpg" width="870px" height="540px" alt="" /></div>
        </div>

        <div className="container">
          <Footer />
        </div>
      </div>
    );
  }

}

export default withRouter(Careers);



