import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';

import Footer from '../../containers/footer/Footer';
import Menu from '../../containers/menu/Menu';

import Hamburger from '../../components/hamburger/Hamburger';
import HomeButton from '../../components/buttons/HomeButton';

class Taskhuntersapp extends Component {
  componentDidMount() {
    window.sr.reveal(".cover", window.cover);

    window.sr.reveal('.immagine', window.immagini);
    window.sr.reveal('.image', window.immagini);

    window.sr.reveal(".herotitle", window.herotitle);
    window.sr.reveal(".description", window.herotitle);

    window.sr.reveal(".text", window.text);

    window.sr.reveal(".sectitle", window.sectitle);
    window.sr.reveal(".freccia", window.arrow);
    window.sr.reveal('.qanda', window.qanda, 100);
    window.sr.reveal('.project', window.projects, 100);
    window.sr.reveal(".contact", window.contact);
    window.sr.reveal(".footer", window.footer, 120);
  }

  // componentWillUnmount() {
  //   setTimeout(() => window.location.reload(), 100);
  //   setTimeout(() => window.scrollTo(0, 0), 110);
  // }

  render() {
    const { history } = this.props;
    return (
      <div id="main">
        <Menu />
        <div className="container">
          <Hamburger />
          <HomeButton />
        </div>

        <div class="projectpage">
          {/* <a class="freccia" href="#list"></a> */}
          <div class="hero taskhuntersapp">
            <div class="container">
              {/* <div id="page-title" class="home hidden">
                <a href="../../en/index.html" class="name">mono. <span>studio</span></a>
              </div> */}
              <h1 onClick={() => history.push('/')} className="brand" style={{ cursor: 'pointer' }}>laon.</h1>
            </div>
          </div>

          <div class="container paragrafo">
            <h2 class="projecttitle herotitle col-xs-10 col-sm-8 mx-auto">Taskhunters app is a virtual community that lets busy people find students and assign them tasks to be solved.</h2>
            <p class="description col-xs-10 col-sm-8 mx-auto">The app was hard to use and understand. There were a lot of students interested in doing tasks but on the other side people were scaried and not easliy involved in creating tasks. Also a lot of people didn't trust the platform. We have simplified all user flows and designed an eye-catching visual restyling. The restyling allowed us to make the platform reassuring. With the redesign of all the flows we have introduced features that allow users to find out more information and get in touch with each other in an easier and more direct way.</p>
          </div>

          <div class="img_full secondcover">
            <img class="immagine" src="/images/projects/taskhuntersapp/02_cover_secondaria@2x.png" alt="taskhuntersapp app Cover" />
          </div>

          <div class="container paragrafo">
            <p class="text col-xs-10 col-sm-8 mx-auto">We started from the basics, after analyzing the flows and the functionalities we went on to verify that the design guidelines had a solid foundation. We have made the color palette more homogeneous by slightly veering the colors and completely revised the typographic part of the app.</p>
          </div>
          <div class="img_background">
            <img class="immagine" src="/images/projects/taskhuntersapp/03_design_guidelines.png" alt="taskhuntersapp app Design guidelines" width="780px" height="1017px" />
          </div>

          <div class="container paragrafo">
            <p class="text col-xs-10 col-sm-8 mx-auto">To design well we are convinced we need a method, our plans to create a design system that contains all the elements for the design of the product. The design system must transmit the how, but also the why, so that the elements are consistent on their own, but specially together.</p>
          </div>
          <div class="img_background colore taskhuntersapp">
            <img class="immagine" src="/images/projects/taskhuntersapp/04_ui_kit.png" alt="taskhuntersapp UI Kit" width="1206px" height="1692px" />
          </div>

          <div class="container paragrafo">
            <p class="text col-xs-10 col-sm-8 mx-auto">A large number of users did not proceed to registration, probably confused by the fact of being able to register both as Tasker (the one who performs the tasks) and Requester (those who instead request it). We simplified the process, eliminated the superfluous elements and simplified the interface to a minimum, ensuring that the result was clean and tidy but pleasant.</p>
          </div>
          <div class="img_background colore taskhuntersapp">
            <img class="immagine" src="/images/projects/taskhuntersapp/05_login_registrazione.png" alt="taskhuntersapp Login registrazione" width="749px" height="1591px" />
          </div>

          <div class="container paragrafo">
            <p class="text col-xs-10 col-sm-8 mx-auto">With the help of the talented <a href="https://giuliazoavo.com" target="_blank" rel='noopener noreferrer'>Giulia Zoavo</a> we gave more character to taskhunters thanks to her wonderful illustrations. It was decided to welcome newly registered users, explaining in three quick steps what the app can do.</p>
          </div>
          <div class="img_background colore taskhuntersapp">
            <img class="immagine" src="/images/projects/taskhuntersapp/06_tutorial.png" alt="taskhuntersapp Tutorial" width="1159px" height="771px" />
          </div>

          <div class="container paragrafo">
            <p class="text col-xs-10 col-sm-8 mx-auto">Analyzes users were suspicious especially in the creation of the task. We have completely redesigned the initial screen for requester users. We designed it to eliminate the confusion of the old version, making it neat and clean. <a href="https://giuliazoavo.com" rel='noopener noreferrer' target="_blank">Giulia Zoavo</a>'s illustrations with the colors of the categories make this screen very pleasant and the data proved us right, the number of tasks created increased immediately.</p>
          </div>
          <div class="img_background colore taskhuntersapp">
            <img class="immagine" src="/images/projects/taskhuntersapp/07_requester_home.png" alt="taskhuntersapp Task creation" width="749px" height="927px" />
          </div>

          <div class="container paragrafo">
            <p class="text col-xs-10 col-sm-8 mx-auto">Another problem we solved is the confusion between the various states of a task. We made sure that the tasks were all within the same screen and we divided them with an always present filter. We also changed the task card by showing only important information based on the state it is in.</p>
          </div>
          <div class="img_background colore taskhuntersapp">
            <img class="immagine" src="/images/projects/taskhuntersapp/08_task.png" alt="taskhuntersapp Task status" width="749px" height="771px" />
          </div>

          <div class="container paragrafo">
            <p class="text col-xs-10 col-sm-8 mx-auto">Regarding the confusion between different types of users we made sure that the user in tasker mode has a colored tab bar while the requester will have a white colored tab bar. We also redesigned map pins and task preview in map search mode.</p>
          </div>
          <div class="img_background colore taskhuntersapp">
            <img class="immagine" src="/images/projects/taskhuntersapp/09_task_tasker.png" alt="taskhuntersapp Tasker" width="749px" height="771px" />
          </div>

          <div class="container paragrafo">
            <p class="text col-xs-10 col-sm-8 mx-auto">For the problem concerning user trust we made sure that both types of users have a complete profile with all the information that can give security and confidence. Students have a list of checks, such as mobile number, university mail or link to the facebook account as well as reviews for each task that has done. The requester user has a list of the tasks he has created and also for him a list of the reviews he has received. For both users it is also reported how long ago they were online the last time and an average response time.</p>
          </div>
          <div class="img_background colore taskhuntersapp">
            <img class="immagine" src="/images/projects/taskhuntersapp/10_profile.png" alt="taskhuntersapp Profiles"  width="749px" height="963px" />
          </div>

          <div class="container paragrafo">
            <p class="text col-xs-10 col-sm-8 mx-auto">The visualization experience was identical both for those who had created the task and for those who wanted perform it, this was not good. We made the experience better, the interface is not completely different, indeed very similar, but the user requester has the ability to quickly modify the task created and to see the offers received as well as accepting or rejecting them. The tasker user, on the other hand, has a quick link to the profile of the user who created the task, and a preview on the map where the task will be performed.</p>
          </div>
          <div class="img_background colore taskhuntersapp">
            <img class="immagine" src="/images/projects/taskhuntersapp/11_dettaglio_task.png" alt="taskhuntersapp Task detail" width="749px" height="863px" />
          </div>


          <div class="container paragrafo">
            <p class="text col-xs-10 col-sm-8 mx-auto">One of the improvements we are most proud of is conversations. We have made them cleaner and more readable but the best is yet to come. We have made sure that all requests made by users appear within the conversation. Users are then able to see a preview of the offer received (or made) and able to accept or reject directly frome here without necessarily having to go back to the task detail.</p>
          </div>
          <div class="img_background colore taskhuntersapp">
            <img class="immagine" src="/images/projects/taskhuntersapp/12_chat.png" alt="taskhuntersapp Chat" width="749px" height="772px" />
          </div>

          <div class="container paragrafo">
            <p class="text col-xs-10 col-sm-8 mx-auto">Communications with users are really important, so we designed different e-mail templates for any tipe of notification o news.</p>
          </div>
          <div class="img_full">
            <img class="immagine" src="/images/projects/taskhuntersapp/13_email_templates@2x.png" alt="taskhuntersapp email templates" />
          </div>

          <div class="img_full">
            <img class="immagine" src="/images/projects/taskhuntersapp/14_cover_finale@2x.png" alt="taskhuntersapp cover finale" />
          </div>
        </div>

        <div className="container">
          <Footer />
        </div>

      </div>
    );
  }

}

export default withRouter(Taskhuntersapp);



